

























































































import { Component, Vue } from 'vue-property-decorator';
import { TechsSummary, TechsSummaryItem, WorkExperience, LocalStorageKey, TechItems } from './Meta';
import { CommonService } from './Util';
import AdsPage from './Ads.vue';

@Component({
    components: {
        AdsPage
    }
})
export default class TechsSummaryPage extends Vue {
    techItems = [ ...[{header: "選択または入力して作成"}], ...TechItems];

    data: TechsSummary = {items: []};
    changeData() {
        localStorage.setItem(LocalStorageKey["TechsSummary"], JSON.stringify(this.data));
    }
    
    workRolls: TechsSummaryItem[] = [];
    workTechs: TechsSummaryItem[] = [];
    applyWe(we: WorkExperience) {
        const res = CommonService.we2ts(we);
        this.workRolls = res[0]
        this.workTechs = res[1];
    }

    selfPlusOnClick() {
        this.data.items.push({index: this.data.items.length, years: 0, months: 0});
        this.changeData();
    }
    selfMinusOnClick(index: number) {
        this.data.items.splice(index, 1);
        this.selfUpdateIndex();
        this.changeData();
    }
    selfUpdateIndex() {
        this.data.items.forEach((value, index) => value.index = index);
    }

    created() {
        this.refreshAll();
    }
    refreshAll() {
        const ts = localStorage.getItem(LocalStorageKey["TechsSummary"]);
        if (ts) this.data = JSON.parse(ts);

        this.refreshWe();
    }
    refreshWe() {
        const we = localStorage.getItem(LocalStorageKey["WorkExperience"]);
        if (we) this.applyWe(JSON.parse(we));
    }
}
