





















































































































import { Component, Vue } from 'vue-property-decorator';
import { WorkExperience, LocalStorageKey, TechItems, RollItems } from './Meta';
import AdsPage from './Ads.vue';

@Component({
    components: {
        AdsPage
    }
})
export default class WorkExperiencePage extends Vue {
    rollItems = [ ...[{header: "選択または入力して作成"}], ...RollItems];
    techItems = [ ...[{header: "選択または入力して作成"}], ...TechItems];
    
    data: WorkExperience = {items: [{index: 0, techs: [], now: false}]};
    changeData() {
        localStorage.setItem(LocalStorageKey["WorkExperience"], JSON.stringify(this.data));
    }

    selectedWindow = 0;
    showMenus: boolean[][] = [];

    plusOnClick() {
        this.data.items.push({index: this.data.items.length, techs: [], now: false});
        this.showMenus.push([false, false]);
        this.changeData();
        this.$nextTick(() => {
            this.selectedWindow = this.data.items.length - 1;
        });
    }
    minusOnClick(index: number) {
        this.data.items.splice(index, 1);
        this.showMenus.splice(index, 1);
        this.updateIndex();
        this.changeData();
    }
    updateIndex() {
        this.data.items.forEach((value, index) => value.index = index);
    }

    termValue(value: string, now: boolean = false): string {
        return now? "現在": (value? value: "0000-00");
    }

    created() {
        this.refreshAll();
    }
    refreshAll() {
        const we = localStorage.getItem(LocalStorageKey["WorkExperience"]);
        if (we) this.data = JSON.parse(we);

        this.data.items.forEach(it => {
            this.showMenus.push([false, false]);
        });
    }
}
