









































import { Component, Vue } from "vue-property-decorator";
import dayjs from 'dayjs';
import AboutThisPage from "./components/AboutThis.vue";
import PersonalInformationPage from "./components/PersonalInformation.vue";
import WorkExperiencePage from "./components/WorkExperience.vue";
import TechsSummaryPage from "./components/TechsSummary.vue";
import { LocalStorageKey } from "./components/Meta";
import { CommonService, FileUtil } from "./components/Util";
import { 
  templateBase,
  contentPersonalInformationTop,
  contentPersonalInformationAddress,
  contentPersonalInformationEbs,
  contentPersonalInformationEbs2,
  contentPersonalInformationBottom,
  contentWorkExperienceTop,
  contentWorkExperience,
  contentTechsSummary,
} from "./components/PdfTemplate"

@Component({
  components: {
    AboutThisPage,
    PersonalInformationPage,
    WorkExperiencePage,
    TechsSummaryPage,
  },
})
export default class App extends Vue {
  tab = null;

  $refs!: {
    fileInput: HTMLInputElement;
    pip: PersonalInformationPage;
    wep: WorkExperiencePage;
    tsp: TechsSummaryPage;
  };

  importData() {
    this.$refs.fileInput.click();
  }
  changeFile(e: any) {
    const file = e.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = () => {
        const data = JSON.parse(reader.result as string);
        if (data.personalInformation) localStorage.setItem(LocalStorageKey["PersonalInformation"], data.personalInformation);
        else localStorage.removeItem(LocalStorageKey["PersonalInformation"]);
        if (data.workExperience) localStorage.setItem(LocalStorageKey["WorkExperience"], data.workExperience);
        else localStorage.removeItem(LocalStorageKey["WorkExperience"]);
        if (data.techsSummary) localStorage.setItem(LocalStorageKey["TechsSummary"], data.techsSummary);
        else localStorage.removeItem(LocalStorageKey["TechsSummary"]);

        const pip = this.$refs.pip;
        if (pip) pip.refreshAll();
        const wep = this.$refs.wep;
        if (wep) wep.refreshAll();
        const tsp = this.$refs.tsp;
        if (tsp) tsp.refreshAll();

        this.$refs.fileInput.value = "";
      };
      reader.onerror = () => {};
      reader.readAsText(file);
    }
  }

  exportData() {
    const pi = localStorage.getItem(LocalStorageKey["PersonalInformation"]);
    const we = localStorage.getItem(LocalStorageKey["WorkExperience"]);
    const ts = localStorage.getItem(LocalStorageKey["TechsSummary"]);

    FileUtil.downloadJSon({
      personalInformation: pi,
      workExperience: we,
      techsSummary: ts
    }, "data.json");
  }

  onSelectTs() {
    const tsp = this.$refs.tsp;
    if (tsp) tsp.refreshWe();
  }

  outputPDF() {
    const Handlebars = require("handlebars/dist/handlebars");

    const piStr = localStorage.getItem(LocalStorageKey["PersonalInformation"]);
    const pi = piStr? JSON.parse(piStr): {};
    const now = dayjs();
    pi.now = `${now.year()}年${now.month() + 1}月${now.date()}日　現在`;
    if (pi.birthday) {
      const spl = pi.birthday.split("-");
      pi.birthdayValue = `${spl[0]}年${spl[1]}月${spl[2]}日`;
      pi.age = dayjs().diff(dayjs(pi.birthday), "years");
    }
    if (pi.educationalBackgrounds) {
      pi.educationalBackgrounds.forEach((it: any) => {
        if (it.start) {
          const spl = it.start.split("-");
          it.startValue = `${spl[0]}年${spl[1]}月`;
        }
        if (it.end) {
          const spl = it.end.split("-");
          it.endValue = `${spl[0]}年${spl[1]}月`;
        }
      });
    }
    if (pi.licenses) {
      pi.licenses.forEach((it: any) => {
        if (it.date) {
          const spl = it.date.split("-");
          it.dateValue = `${spl[0]}年${spl[1]}月`;
        }
      });
    }
    pi.spouseValue = pi.spouse? "有": "無";
    pi.memoValue = pi.memo? pi.memo.replaceAll("\n", "\\n"): "";
    
    const weStr = localStorage.getItem(LocalStorageKey["WorkExperience"]);
    const we = weStr? JSON.parse(weStr): {};
    if (we.items) {
      we.items.forEach((it: any) => {
        if (it.start) {
          const spl = it.start.split("-");
          it.startValue = `${spl[0]}年${spl[1]}月`;
        }
        if (it.now) {
          it.endValue = "現在";
        } else if (it.end) {
          const spl = it.end.split("-");
          it.endValue = `${spl[0]}年${spl[1]}月`;
        }
        if (it.value) {
          it.valueValue = it.value.replaceAll("\n", "\\n");
        }
      });
    }

    const tsStr = localStorage.getItem(LocalStorageKey["TechsSummary"]);
    const ts = tsStr? JSON.parse(tsStr): {};
    const res = CommonService.we2ts(we);
    ts.workRolls = res[0];
    ts.workTechs = res[1];

    const baseDef = JSON.parse(templateBase);
    const contentPersonalInformationTopDef = JSON.parse(Handlebars.compile(contentPersonalInformationTop)(pi));
    const contentPersonalInformationAddressDef = JSON.parse(Handlebars.compile(contentPersonalInformationAddress)(pi));
    const contentPersonalInformationEbsDef = JSON.parse(Handlebars.compile(contentPersonalInformationEbs)(pi));
    const contentPersonalInformationEbs2Def = JSON.parse(Handlebars.compile(contentPersonalInformationEbs2)(pi));
    const ccontentPersonalInformationBottomDef = JSON.parse(Handlebars.compile(contentPersonalInformationBottom)(pi));
    const contentWorkExperienceTopDef = JSON.parse(Handlebars.compile(contentWorkExperienceTop)(we));
    const contentWorkExperienceDef = JSON.parse(Handlebars.compile(contentWorkExperience)(we));
    const contentTechsSummaryDef = JSON.parse(Handlebars.compile(contentTechsSummary)(ts));
    baseDef.content = [
      ...baseDef.content,
      ...contentPersonalInformationTopDef.content,
      ...contentPersonalInformationAddressDef.content,
      ...contentPersonalInformationEbsDef.content,
      ...contentPersonalInformationEbs2Def.content,
      ...ccontentPersonalInformationBottomDef.content,
      ...contentWorkExperienceTopDef.content,
      ...contentTechsSummaryDef.content,
      ...contentWorkExperienceDef.content,
    ];

    const pdfMake = require('pdfmake/build/pdfmake.js');
    const pdfFonts = require('pdfmake/build/vfs_fonts.js');
    pdfMake.vfs = pdfFonts.pdfMake.vfs;
    pdfMake.fonts = {
      SawarabiMincho: {
        normal: "SawarabiMincho-Regular.ttf",
        bold: "SawarabiMincho-Regular.ttf",
        italics: "SawarabiMincho-Regular.ttf",
        bolditalics: "SawarabiMincho-Regular.ttf",
      },
    };
    pdfMake.tableLayouts = {
      thinBorder: {
        hLineWidth: () => 0.5, 
        vLineWidth: () => 0.5
      }
    };
    pdfMake.createPdf(baseDef).open();
  }
}
