import dayjs from 'dayjs';
import { saveAs } from 'file-saver';
import { TechsSummaryItem, WorkExperience } from './Meta';

export class FileUtil {
    public static downloadJSon(jsonObject: any, filename: string) {
        const blob = new Blob([JSON.stringify(jsonObject, null, 2)], {
            type: 'application/json',
        });
        saveAs(blob, filename);
    }
}

export class CommonService {
    public static we2ts(we: WorkExperience): TechsSummaryItem[][] {
        const workRolls: TechsSummaryItem[] = [];
        const workTechs: TechsSummaryItem[] = [];
        we.items.forEach(it => {
            if (it.start && it.end) {
                const diff = dayjs(it.end).diff(dayjs(it.start), "month")
                if (diff >= 0) {
                    if (it.roll) {
                        let item: TechsSummaryItem|undefined = workRolls.find(r => r.name == it.roll);
                        if (!item) {
                            item = {index: workRolls.length, name: it.roll, years: 0, months: 0}
                            workRolls.push(item);
                        }
                        this.addTerm(item, diff);
                    }
                    it.techs.forEach(tech => {
                        let item: TechsSummaryItem|undefined = workTechs.find(r => r.name == tech);
                        if (!item) {
                            item = {index: workTechs.length, name: tech, years: 0, months: 0}
                            workTechs.push(item);
                        }
                        this.addTerm(item, diff);
                    });
                }
            }
        });

        return [workRolls, workTechs];
    }
    private static addTerm(item: TechsSummaryItem, diff: number) {
        item.years += Math.floor(diff/12);
        item.months += diff % 12;
        if (item.months >= 12) {
            item.years += 1;
            item.months -= 12;
        }
    }
}