


























































































































































































































































































































































import { Component, Vue, Watch } from 'vue-property-decorator';
import { PersonalInformation, LocalStorageKey, GenderItems, LanguageItems, LanguageLevelItems } from './Meta';
import axios from 'axios';
import dayjs from 'dayjs';
import AdsPage from './Ads.vue';

@Component({
    components: {
        AdsPage
    }
})
export default class PersonalInformationPage extends Vue {
    genderItems = GenderItems;
    languageItems = [ ...[{header: "選択または入力して作成"}], ...LanguageItems];
    languageLevelItems = [ ...[{header: "選択または入力して作成"}], ...LanguageLevelItems];

    data: PersonalInformation = {educationalBackgrounds: [], licenses: [], languages: [], spouse: false};
    changeData() {
        localStorage.setItem(LocalStorageKey["PersonalInformation"], JSON.stringify(this.data));
    }

    showBirthdayMenu = false;
    birthdayCalendarActivePicker: string|null = null;
    age: number|null = null;
    @Watch("showBirthdayMenu")
    showBirthdayMenuChange(value: boolean) {
        value && this.$nextTick(() => this.birthdayCalendarActivePicker = "YEAR");
    }
    changeBirthday() {
        this.changeData();
        this.showBirthdayMenu = false;
        this.applyAge();
    }
    applyAge() {
        if (this.data.birthday) {
            this.age = dayjs().diff(dayjs(this.data.birthday), "years");
        }
    }

    lastValue: string = "";
    changeZipCode(value: string) {
        this.changeData();
        const num = value.replaceAll("-", "");
        if (num.length == 7 && value != this.lastValue) {
            axios.get("https://zipcloud.ibsnet.co.jp/api/search?zipcode=" + num)
                .then(res => {
                    if (res.status == 200 && res.data.results) {
                        const results = res.data.results[0];
                        Vue.set(this.data, "address", results.address1 + results.address2 + results.address3);
                    }
                })
                .catch(err => console.log(err));
        }
        this.lastValue = value;
    }

    showEbMenus: boolean[][] = [];
    showLiMenus: boolean[] = [];

    ebPlusOnClick() {
        this.data.educationalBackgrounds.push({index: this.data.educationalBackgrounds.length});
        this.showEbMenus.push([false, false]);
        this.changeData();
    }
    ebMinusOnClick(index: number) {
        this.data.educationalBackgrounds.splice(index, 1);
        this.showEbMenus.splice(index, 1);
        this.ebUpdateIndex();
        this.changeData();
    }
    ebUpdateIndex() {
        this.data.educationalBackgrounds.forEach((value, index) => value.index = index);
    }

    liPlusOnClick() {
        this.data.licenses.push({index: this.data.licenses.length});
        this.showLiMenus.push(false);
        this.changeData();
    }
    liMinusOnClick(index: number) {
        this.data.licenses.splice(index, 1);
        this.showLiMenus.splice(index, 1);
        this.liUpdateIndex();
        this.changeData();
    }
    liUpdateIndex() {
        this.data.licenses.forEach((value, index) => value.index = index);
    }

    laPlusOnClick() {
        this.data.languages.push({index: this.data.languages.length});
        this.changeData();
    }
    laMinusOnClick(index: number) {
        this.data.languages.splice(index, 1);
        this.laUpdateIndex();
        this.changeData();
    }
    laUpdateIndex() {
        this.data.languages.forEach((value, index) => value.index = index);
    }

    spouseValue(): string {
        return this.data.spouse? "有": "無";
    }

    created() {
        this.refreshAll();
    }
    refreshAll() {
        const pi = localStorage.getItem(LocalStorageKey["PersonalInformation"]);
        if (pi) {
            this.data = JSON.parse(pi);
            this.applyAge();
        }

        this.data.educationalBackgrounds.forEach(it => {
            this.showEbMenus.push([false, false]);
        });
        this.data.licenses.forEach(it => {
            this.showLiMenus.push(false);
        });
    }
}
